.whoops {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-image: url(/images/404.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}
@media screen and (max-width: 450px) {
  .whoops {
    background-position: center left;
  }
}
.whoops__card {
  margin: 1em;
  box-shadow: 0px 6px 8px 8px rgba(8, 1, 128, 0.2);
  max-width: 500px;
  background-color: white;
  border-top: 1em solid #A91E24;
  padding: 1em;
  border-radius: 3px;
}
.whoops__card h1 {
  margin: 0;
  white-space: initial !important;
}
.whoops__card a {
  margin-top: 0.5em;
  display: block;
  text-align: center;
}